/* Header
==================== */
.header {
	width: 100%;
	height: 100px;

	position: absolute;
	top: 0;
	left: 0;
	z-index: 9999;

	&__inner {
		display: flex;
		justify-content: space-between;
		align-items: center;

		padding: 35px 0;
		border-bottom: 1px solid #515369;
	}

	&.fixed {
		height: auto;

		position: fixed;

		background-color: #31344e;

		& .header__inner {
			padding-top: 15px;
			padding-bottom: 15px;

			border-bottom: 0;

			transition: 0.3s linear;
		}
	}
}


/* Nav
==================== */
.nav {
	display: flex;

	font-family: 'Raleway Bold', sans-serif;
	font-size: 13px;
	font-weight: 700;
	text-transform: uppercase;

	&__link {
		margin-left: 50px;

		color: #fff;
		text-decoration: none;
		opacity: 0.75;

		transition: opacity 0.1s linear;

		&:first-child {
			margin-left: 0;
		}

		&:hover {
			opacity: 1;
		}
	}
}


/* Intro
==================== */
.intro {
	display: flex;
	flex-direction: column;
	justify-content: center;

	height: 750px;
	padding-top: 100px;

	background: #31344e url("../img/intro-bg.jpg") center no-repeat;
	background-size: cover;

	&__inner {
		width: 100%;
		max-width: 970px;
		margin: 0 auto;

		text-align: center;
	}

	&__title {
		margin: 0 0 30px;

		color: #fff;
		font-family: 'OpenSans Bold', sans-serif;
		font-size: 65px;
		line-height: 1.1;
		text-transform: uppercase;
	}

	&__subtitle {
		margin-bottom: 60px;

		color: #fff;
		font-family: 'Raleway Regular', sans-serif;
		font-weight: 400;
		font-size: 22px;
		line-height: 1.5;
	}
}


/* Button
===================*/
.btn {
	display: inline-block;
	vertical-align: top;
	padding: 11px 40px;

	border-radius: 2px;
	border: 0;
	cursor: pointer;

	color: #fff;
	font-family: 'Raleway Semibold', sans-serif;
	font-size: 13px;
	line-height: 21px;
	text-transform: uppercase;
	text-align: center;

	transition: background 0.1s linear;

	&--red {
		background-color: #e84545;

		&:hover {
			background-color: #dd3434;
		}
	}

	&--long {
		min-width: 280px;
	}
}


/* Features
===================*/
.features {
	display: flex;
	flex-wrap: wrap;

	margin: 95px 0;

	&__item {
		width: 33.33333%;
		padding: 0 40px;
		margin: 25px 0;

		text-align: center;
	}

	&__icon {
		margin-bottom: 35px;
	}

	&__title {
		margin-bottom: 25px;

		color: #2d3033;
		font-family: 'Raleway Regular', sans-serif;
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
	}

	&__text {
		color: #6c7279;
		font-family: 'Raleway Regular', sans-serif;
		font-size: 14px;
		font-weight: 400;
		line-height: 21px;
	}
}


/* Works
===================*/
.works {

	display: flex;
	flex-wrap: wrap;

	&__item {
		width: 25%;
		height: 350px;

		border-bottom: 1px solid #fff;
		border-right: 1px solid #fff;

		position: relative;
		overflow: hidden;

		&:hover .works__content {
			opacity: 1;
		}
	}

	&__photo {
		min-width: 100%;

		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 1;

		transform: translate3d(-50%, -50%, 0);
	}

	&__content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		width: 100%;
		height: 100%;

		background-color: rgba(232, 69, 69, 0.9);
		opacity: 0;
		cursor: pointer;
		
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;

		transition: opacity 0.2s linear;
	}
	
	&__title {
		margin-bottom: 15px;

		color: #fff;
		font-family: 'Raleway Regular', sans-serif;
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
	}

	&__text {
		color: #fff;
		font-family: 'Raleway Regular', sans-serif;
		font-size: 14px;
		font-weight: 400;
	}
}


/* Team
===================*/
.team {

	margin: 100px 0 70px;
	
	&__inner {
		display: flex;
		flex-wrap: wrap;

		margin: 0 -15px;
	}

	&__item {
		width: 25%;
		padding: 0 15px;
		margin-bottom: 30px;
	}

	&__photo {
		display: block;
		max-width: 100%;
		height: auto;

		margin-bottom: 30px;
	}

	&__name {
		margin-bottom: 15px;

		color: #2d3033;
		font-family: 'Raleway Regular', sans-serif;
		font-size: 22px;
		font-weight: 400;
	}

	&__prof {
		margin-bottom: 30px;

		color: #e84545;
		font-family: 'Raleway Regular', sans-serif;
		font-size: 13px;
		font-weight: 400;
		text-transform: uppercase;
	}

	&__text {
		margin-bottom: 30px;

		color: #6c7279;
		font-family: 'Raleway Regular', sans-serif;
		font-size: 14px;
		font-weight: 400;
		line-height: 21px;
	}
}


/* Social
===================*/
.social {

	display: flex;

	&__item {
		display: flex;
		justify-content: center;
		align-items: center;
		width: 40px;
		height: 40px;
		margin-right: 4px;

		background-color: #fff;
		border: 1px solid #e8ecee;

		transition: background 0.1s linear;

		&:hover {
			background-color: #e8ecee;
		}

		&:hover .social__icon {
			fill: #000;
		}
	}

	&__icon {
		display: block;
		height: 18px;

		fill: #c6cacc;

		transition: fill 0.1s linear
	}

	&--footer {
		justify-content: center;

		.social__item {
			background-color: transparent;
			border-width: 2px;
			border-color: #fff;
			border-radius: 2px;

			&:hover {
				background-color: #fff;
			}
		}

		.social__icon {
			fill: #fff;
		}
	}

	&__heart {
		width: 14px;
		height: 10px;
	}
}


/* Reviews
===================*/
.reviews {
	overflow: hidden;
	background-color: #53354a;

	&__item {
		display: flex;
		flex-wrap: wrap;
	}

	&__photo {
		position: relative;
		width: 50%;
		height: 500px;
	}

	&__img {
		position: absolute;
		top: 50%;
		right: 0;
		z-index: 1;

		transform: translateY(-50%);
	}

	&__content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		width: 50%;
		padding-left: 85px;
	}

	&__text {
		margin-bottom: 30px;

		color: #fff;
		font-family: 'Cardo Italic', sans-serif;
		font-size: 36px;
		font-style: italic;
		line-height: 1.2;
	}

	&__author {
		color: #fff;
		font-family: 'Raleway Regular', sans-serif;
		font-size: 13px;
		font-weight: 700;
		text-transform: uppercase;
	}
}


/* Download
===================*/
.download {

	margin: 100px 0;
	text-align: center;

	&__title {
		margin-bottom: 15px;

		color: #2d3033;
		font-family: 'Raleway Light', sans-serif;
		font-size: 28px;
		font-weight: 300;
		line-height: 21px;
	}

	&__text {
		margin-bottom: 30px;

		color: #2d3033;
		font-family: 'Raleway Bold', sans-serif;
		font-size: 13px;
		font-weight: 700;
		line-height: 21px;
		text-transform: uppercase;
	}
}


/* Footer
===================*/
.footer {
	background-color: #3a3e64;

	&__inner {
		display: flex;
		flex-wrap: wrap;
		padding: 70px 0;
	}

	&__block {
		width: 33.33333%;
		padding: 0 15px;

		text-align: center;
	}

	&__title {
		margin-bottom: 10px;

		color: #fff;
		font-family: 'Raleway Bold', sans-serif;
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
	}

	&__address {
		opacity: 0.5;
		color: #fff;
		font-family: 'Raleway Regular', sans-serif;
		font-size: 14px;
		font-weight: 400;
		font-style: normal;
		line-height: 22.5px;
	}

	&__text {
		opacity: 0.5;
		color: #fff;
		font-family: 'Raleway Regular', sans-serif;
		font-size: 14px;
		font-weight: 400;
		line-height: 22.5px;
	}
}


/* Copyright
===================*/
.copyright {
	padding: 20px 0;

	background-color: #313454;

	&__text {
		text-align: center;
		color: #808080;
		font-family: 'Raleway Regular', sans-serif;
		font-size: 14px;
		font-weight: 400;
		line-height: 22.5px;

		span {
			color: #fff;
		}
	}
}


/* Burger
===================*/
.burger {
	display: none;
	padding: 9px 2px;

	background: none;
	border: 0;
	cursor: pointer;

	&__item {
		position: relative;

		display: block;
		width: 28px;
		height: 3px;

		background-color: #fff;

		font-size: 0;
		color: transparent;

		&:before, &:after {
			content: "";
			height: 100%;

			position: absolute;
			right: 0;
			z-index: 1;

			background-color: #fff;
		}

		&:before {
			width: 30px;
			top: -8px;
		}

		&:after {
			width: 26px;
			bottom: -8px;
		}
	}
}



/* Media
==================== */
// max-width: 1300px;
// max-width: 991px;
// max-width: 767px;
// max-width: 414px;



/* Media
===================*/
@media(max-width: 991px) {

	/* Nav */
	.nav {
		display: none;
	}

	/* Burger */
	.burger {
		display: block;
	}

	/* Intro */
	.intro__title {
		font-size: 40px;
	}

	/* Features */
	.features__item {
		width: 50%;
	}

	/* Works */
	.works__item {
		width: 50%;
	}

	/* Team */
	.team {

		&__item {
			width: 50%;

			text-align: center;
		}

		&__photo {
			margin-left: auto;
			margin-right: auto;
		}

	}

	/* Social */
	.social {
		justify-content: center;
	}

	/* Reviews */
	.reviews {

		&__photo {
			width: 100%;
			margin: 15px 0 40px;

			overflow: hidden;
		}

		&__content {
			width: 100%;
			padding-left: 0;
			padding-bottom: 40px;
		}
	}
}



@media(max-width: 767px) {

	/* Intro */
	.intro {
		height: auto;
		padding-top: 130px;
		padding-bottom: 30px;

		&__title {
			font-size: 30px;
		}

		&__subtitle {
			margin-bottom: 30px;

			font-size: 16px;
		}
	}

	/* Features */
	.features {

		margin: 40px 0;

		&__item {
			width: 100%;
			padding-left: 0;
			padding-right: 0;
		}
	}

	/* Works */
	.works__item {
		width: 100%;
		height: 300px;
	}

	/* Team */
	.team {

		margin: 60px 0 30px;

		&__item {
			width: 100%;
		}
	}

	/* Reviews */
	.reviews__text {
		font-size: 25px;
	}

	/* Download */
	.download {

		margin: 50px 0 50px;

		&__title {
			font-size: 24px;
		}

	}

	/* Btn */
	.btn--long {
		min-width: 260px;
	}

	/* Footer */
	.footer {
		
		&__inner {
			padding: 30px 0;
		}

		&__block {
			width: 100%;
			padding-left: 0;
			padding-right: 0;
			margin-bottom: 30px;

			&:last-child {
				margin-bottom: 0;
			}
		}

	}
	
}



@media (max-width: 414px) {

	/* Reviews */
	.reviews__img {
		right: -100px;
	}

}